@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,700");
@import url("https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap");

@font-face {
  font-family: 'Bubblegum Sans', cursive;
  src: url("../fonts/Bubblegum_Sans.woff") format("woff");
  font-weight: normal;
}

@font-face {
  font-family: 'Connie';
  src: url("../fonts/Connie_Regular.woff") format("woff");
  font-weight: normal;
}

body {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: Raleway;
    color: black;
    background-image: url("../images/Sky2.jpg");
    background-size: cover;
  }
  
  html, body {
    margin: 0;
    padding: 0;
    min-height: 100%;
  }
  p{
    font-size: 1.7em;
    background-color: rgba(255, 255, 255, 0.212);
    background-blend-mode: multiply;
    text-shadow: 1px 1px 25px #faade3;
    max-width: fit-content;
    max-height: fit-content;
    font-weight: bold;
    padding: 0.4em;
    border-radius: 8%;
    margin: 0.7em;
  }
  .nav ul {
    font-family: "Bubblegum Sans";
    margin: 0;
  }
  
  .nav li {
    display: inline;
  }
  
  .nav a {
    display: inline-block;
    padding: .5em;
    color: white;
    text-decoration: none;
  }
  
  .main-nav {
    font-family: "Bubblegum Sans";
    text-align: center;
    font-size: 1.1em;
    font-weight: lighter;
    border-bottom: 3px solid rgba(255, 255, 255, 0.3);
  }
  
  .main-nav li {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: "Bubblegum Sans";
    color: #777;
    padding: 0 5%;
    font-size: 1.5em;
  }
  
  .nav a:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }
  
  .main-header {
    background-color: rgba(216, 91, 189, 0.74);
    background-image: url("../images/Sky3.jpg");
    background-blend-mode: multiply;
    background-size: cover;
  }
  
  .site-name {
    text-align: center;
    margin: 0;
    font-size: 4em;
    font-family: "Connie";
    font-weight: normal;
    color: white;
  }
  
  .site-name-large {
    font-family: "Connie";
    font-size: 8em;
  }
  
  .container {
    background-size: auto;
    margin: 0 auto;
    margin-top: 0;
    padding: 0 1.5em;
  }
  
  .content-section {
    margin: 0;
    margin-bottom: 0;
  }
  
  .section-header {
    font-weight: 900;
    text-align: center;
    text-shadow: 1px 1px 30px #f1bde6;
    color: rgb(29, 11, 99);
    font-size: 3em;
    padding: 0;
    font-family: "Connie";
    margin-bottom: 0;
    margin-top: 0.4em;
  }